import React, { useState } from 'react';
import PropTypes from 'prop-types';
import {
  RatingWrapper,
  Star,
  StarsWrapper,
  RateName,
} from './RatingStars.style';

const RatingStars = ({ starsNumber = 5, rateName }) => {
  const [currentValue, setCurrentValue] = useState(0);
  const [hoverValue, setHoverValue] = useState(null);

  const handleStarClick = (value) => setCurrentValue(value);

  const starsArray = Array(starsNumber).fill(0);

  const renderStars = starsArray.map((star, index) => (
    <Star
      isStarChecked={currentValue > index ? true : false}
      onClick={() => handleStarClick(index + 1)}
      key={index}
    />
  ));
  return (
    <RatingWrapper>
      <RateName>{rateName}</RateName>
      <StarsWrapper>{renderStars}</StarsWrapper>
    </RatingWrapper>
  );
};

RatingStars.propTypes = {};

export default RatingStars;
